// -----------------------------------------------------------------------------
// _highlight.scss
// This file contains all styles related to the highlight (code) component.
// -----------------------------------------------------------------------------

.highlight {
	$self: &;

	border-radius: 4px;
	overflow: hidden;

	// Props
	// =========================================================================

	* {
		color: $black;
	}

	&:last-of-type {
		margin-bottom: 0;
	}

	// Pre
	// =========================================================================

	pre {
		padding: 10px;
		margin: 0;
	}

	// Code
	// =========================================================================

	code {
		font-family: $font-code;
		font-size: 1rem;
	}
}
